@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

html{
    font-size: 62.5%;
    scroll-behavior: smooth;
    font-family: 'Montserrat', sans-serif;
}


/* header */
.navbar {
    @apply flex items-center justify-between w-full fixed top-0 z-10 h-[5.4rem] p-[3.2rem];
}

.navlink{
    @apply text-2xl font-semibold text-[#181b21] mx-[0.8rem] px-[0.8rem] py-[0.4rem] rounded-[0.8rem] bg-transparent hover:bg-[#181b21] hover:bg-opacity-10 transition-all duration-300 ;
}


/*car section */
.car-section{
    @apply w-full h-screen relative;
}
.car-img{
    @apply w-full h-full object-cover;
}
.transform-50{
    transform: translate(-50%,-50%);
}
.car-name{
    @apply text-[4rem] text-[#393c41] font-semibold;
}
.tagline{
    @apply text-[#4c4d51] text-2xl;
}
.car-button{
    @apply absolute top-[85%] left-[50%] flex flex-col lg:flex-row;
}
.btn{
    @apply text-2xl text-white bg-[#171a20] font-medium my-2 py-[0.4rem] px-[2.4rem] rounded-[3.2rem] w-[25.5rem] h-16 bg-opacity-80;
}
.down-svg{
    @apply block w-full h-20 mx-auto absolute top-[93%] animate-bounce;
}